import React from "react";
import { getChapter } from "../../apis";
import { withRouter } from "react-router-dom";
import renderHTML from "react-render-html";
import { IconButton } from "@material-ui/core";

class Chapter extends React.Component {
  state = { chapter: { next: {}, previous: {} } };
  componentWillMount() {
    console.log("mounted");
    getChapter(this.props.match.params.id, this.props.language).then(chapter =>
      this.setState({ chapter })
    );
  }
  componentWillReceiveProps(newProps) {
    if (newProps.language != this.props.language)
      return getChapter(this.props.match.params.id, newProps.language).then(
        chapter => this.setState({ chapter })
      );
  }
  // shouldComponentUpdate(newState, newProps) {
  //   if (newProps.language !== this.props.language) return true;
  //   console.log("update")
  //   return false;
  // }
  render() {
    return (
      <div>
        <span>
          <IconButton
            onClick={() =>
              this.props.history.push(
                "/chapter/" + this.state.chapter.previous.id
              )
            }
            style={{ margin: 1 }}
            aria-label="Delete"
          >
            PREV
          </IconButton>
          <IconButton
            onClick={() =>
              this.props.history.push("/chapter/" + this.state.chapter.next.id)
            }
            style={{ margin: 1, float: "right" }}
            aria-label="Delete"
          >
            NEXT
          </IconButton>
        </span>
        <h4 style={{ textAlign: "center" }}>{this.state.chapter.reference}</h4>

        {renderHTML(this.state.chapter.content || "")}
        {/* {this.state.chapter.content} */}
      </div>
    );
  }
}

export default withRouter(Chapter);
