import axios from "axios";
import config from "../config";
import Images from "./images";

let getBooks = () =>
  new Promise((resolve, reject) => {
    let books = [];
    axios
      .all([
        axios.get(`${config.host}/${config.kjvId}/books`, {
          headers: { "api-key": `${config.apiKey}` }
        }),
        axios.get(
          `${config.host}/${
            config.tamilId
          }/books?include-chapters=true&include-chapters-and-sections=true`,
          {
            headers: { "api-key": `${config.apiKey}` }
          }
        )
      ])
      .then(
        axios.spread((kjv, tamil) => {
          let imgs = [...Images.images];
          imgs.length > 66
            ? (imgs = [...imgs])
            : new Array(Math.floor(66 / imgs.length) + 1)
                .fill(5)
                .map((a, i) => {
                  return (imgs = [...imgs, ...Images.images]);
                });
          // function shuffle(array) {
          //   return array.sort(() => Math.random() - 0.5);
          // }
          // imgs = shuffle(imgs);
          kjv.data.data.map(b => (books = [...books, { english: b.name }]));
          tamil.data.data.map(
            (b, i) =>
              (books = [
                ...books.slice(0, i),
                {
                  ...books[i],
                  id: b.id,
                  tamil: b.name,
                  chapters: b.chapters,
                  img: `${config.imgApi}/${imgs[i]}`
                },
                ...books.slice(i + 1)
              ])
          );

          // console.log(kjv.data, tamil.data);
          return resolve(books);
        })
      )
      .catch(err => {
        console.log(err);
        reject(err);
      });
  });

export default getBooks;
