import axios from "axios";
import config from "../config";
import Images from "./images";

let getSections = book =>
  new Promise((resolve, reject) => {
    let books = [];

    axios
      .get(`${config.host}/${config.tamilId}/books/${book}/sections`, {
        headers: { "api-key": `${config.apiKey}` }
      })
      .then(data => {
        let imgs = [...Images.images];
        imgs.length > data.data.data.length
          ? (imgs = [...imgs])
          : new Array(Math.floor(66 / imgs.length) + 1).fill(5).map((a, i) => {
              return (imgs = [...imgs, ...Images.images]);
            });
        let sections = data.data.data.map((d, i) => ({
          ...d,
          img: `${config.imgApi}/${imgs[i]}`
        }));
        resolve(sections);
      })
      .catch(err => {
        console.log(err);
        reject(err);
      });
  });

export default getSections;
