import React from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import Books from "./views/Books";
import Book from "./views/Book";
import Chapter from "./views/Chapter";
import { getBooks } from "./apis";

import clsx from "clsx";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const drawerWidth = 240;

const useStyles = () => ({
  root: {
    display: "flex",
    marginTop: 50
  },
  appBar: {
    transition:
      "margin 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms,width 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms"
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition:
      "margin 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms,width 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms"
  },
  menuButton: {
    marginRight: 2
  },
  hide: {
    display: "none"
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: "0 8px",
    // ...theme.mixins.toolbar,
    justifyContent: "flex-end"
  },
  content: {
    flexGrow: 1,
    padding: 3,
    transition:
      "margin 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms,width 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms",
    marginLeft: -drawerWidth
  },
  contentShift: {
    transition:
      "margin 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms,width 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms",
    marginLeft: 0
  },
  formControl: {
    margin: 3
  }
});

class Routes extends React.Component {
  state = { open: false, language: "tamil", books: [] };
  componentWillMount() {
    getBooks().then(books => {
      console.log("get books");
      this.setState({ books });
    });
  }

  switchRoutes = (
    <Switch>
      <Route
        path={"/chapter/:id"}
        component={() => (
          <Chapter books={this.state.books} language={this.state.language} />
        )}
      />
      <Route
        path={"/book/:id"}
        component={() => (
          <Book books={this.state.books} language={this.state.language} />
        )}
      />
      {/* <Route
        path={"/"}
        component={() => (
          <Books books={this.state.books} language={this.state.language} />
        )}
      /> */}
    </Switch>
  );
  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };
  render() {
    const { classes } = this.props;
    const { open, books, language } = this.state;

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={clsx(classes.appBar, {
            [classes.appBarShift]: open
          })}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={this.handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap>
              Persistent drawer
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={this.handleDrawerClose}>
              {/* {theme.direction === "ltr" ? ( */}
              <ChevronLeftIcon />
              {/* ) : ( */}
              {/* <Chevro/nRightIcon /> */}
              {/* )} */}
            </IconButton>
          </div>
          <Divider />
          <FormControl className={classes.formControl}>
            <InputLabel htmlFor="Language-helper">Language</InputLabel>
            <Select
              value={language}
              onChange={e => this.setState({ language: e.target.value })}
              input={<Input name="Language" id="Language-helper" />}
            >
              <MenuItem value={"tamil"}>Tamil</MenuItem>
              <MenuItem value={"english"}>English</MenuItem>
            </Select>
            <FormHelperText>Some important helper text</FormHelperText>
          </FormControl>
          <List>
            {books.slice(0, 39).map((book, index) => (
              <ListItem
                button
                key={book.tamil}
                onClick={() =>
                  this.props.history.push("/book/" + book.id, {
                    book: book
                  })
                }
              >
                <ListItemIcon>
                  {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                </ListItemIcon>
                <ListItemText
                  primary={language == "tamil" ? book.tamil : book.english}
                />
              </ListItem>
            ))}
          </List>
          <Divider />
          New Testament
          <List>
            {books.slice(39).map((book, index) => (
              <ListItem
                button
                key={book.tamil}
                onClick={() =>
                  this.props.history.push("/book/" + book.id, {
                    book: book
                  })
                }
              >
                <ListItemIcon>
                  {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                </ListItemIcon>
                <ListItemText
                  primary={language == "tamil" ? book.tamil : book.english}
                />
              </ListItem>
            ))}
          </List>
        </Drawer>
        <main
          className={clsx(classes.content, {
            [classes.contentShift]: open
          })}
        >
          <Books books={this.state.books} language={this.state.language} />
          {this.switchRoutes}
        </main>
      </div>
    );
  }
}

export default withStyles(useStyles)(withRouter(Routes));
