import React from "react";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { withStyles, IconButton } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { getSections } from "../../apis";
import Sections from "./Sections.js";

const useStyles = () => ({
  root: {
    width: "100%"
  },
  heading: {
    fontSize: 15,
    flexBasis: "33.33%",
    flexShrink: 0
  },
  secondaryHeading: {
    fontSize: 15,
    color: "#ddd"
  }
});

class Book extends React.Component {
  state = { book: { chapters: [] }, sections: [], value: 0 };
  componentWillMount() {
    this.props.location.state &&
    this.props.location.state.book &&
    this.props.location.state.book.id
      ? this.setState({ book: this.props.location.state.book })
      : this.props.history.push("/");
    console.log(this.props);
    getSections(this.props.match.params.id).then(sections =>
      this.setState({ sections })
    );
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <Paper square>
          <Tabs
            style={{ justifyContent: "center" }}
            value={this.state.value}
            indicatorColor="primary"
            textColor="primary"
            onChange={(e, v) => this.setState({ value: v })}
          >
            <Tab label="Chapters" />
            <Tab label={this.state.book.tamil} disabled />
            <Tab label="Sections" />
          </Tabs>
        </Paper>
        {this.state.value === 0 &&
          this.state.book.chapters.map(ch => (
            <IconButton
              onClick={() => this.props.history.push("/chapter/" + ch.id)}
              style={{ margin: 1 }}
              aria-label="Delete"
            >
              {ch.number}
            </IconButton>
          ))}
        {this.state.value === 2 && <Sections sections={this.state.sections} />}
      </div>
    );
  }
}

export default withRouter(withStyles(useStyles)(Book));
