import React from "react";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import ListSubheader from "@material-ui/core/ListSubheader";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import { withStyles } from "@material-ui/styles";
import { withRouter } from "react-router-dom";

const useStyles = () => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: "fff"
  },
  gridList: {
    width: 500,
    height: window.innerHeight
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)"
  }
});

class Books extends React.Component {
  state = { books: [] };
  // componentWillMount() {
  //   getBooks().then(books => this.setState({ books }));
  // }

  componentWillMount() {
    console.log("mounted");
  }
  componentWillReceiveProps(newProps) {
    console.log(newProps);
  }
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <GridList cellHeight={180} className={classes.gridList}>
          <GridListTile key="Subheader" cols={2} style={{ height: "auto" }}>
            <ListSubheader component="div">Books Of the Bible</ListSubheader>
          </GridListTile>
          {this.props.books.map((book, i) => (
            <GridListTile key={i}>
              <img src={book.img} alt={book.title} />
              <GridListTileBar
                title={book.tamil}
                subtitle={<span>{book.english}</span>}
                actionIcon={
                  <IconButton
                    onClick={() =>
                      this.props.history.push("/book/" + book.id, {
                        book: book
                      })
                    }
                    className={classes.icon}
                  >
                    <InfoIcon />
                  </IconButton>
                }
              />
            </GridListTile>
          ))}
        </GridList>
      </div>
    );
  }
}

export default withRouter(withStyles(useStyles)(Books));
