import axios from "axios";
import config from "../config";

let getBook = (chapter, language) =>
  new Promise((resolve, reject) => {
    let bibleId = config.tamilId;
    if (language == "tamil") bibleId = config.tamilId;
    if (language == "english") bibleId = config.kjvId;

    console.log(language, bibleId);
    axios
      .get(`${config.host}/${bibleId}/chapters/${chapter}?content-type=html`, {
        headers: { "api-key": `${config.apiKey}` }
      })
      .then(data => resolve(data.data.data))
      .catch(err => {
        console.log(err);
        reject(err);
      });
  });

export default getBook;
